.header {
    width: 100%;
    height: 3rem;
    background-color: #6D7A69;
    padding: 0 2%;
    
  }
  
  .header nav {
    height: 100%;
  }
  
  .header ul {
    height: 100%;
    list-style: none;
    display: flex;
    padding: 5;
    margin: 2;
    align-items: center;
    justify-content: center;
  }
  
  .header li {
    margin: 0 1.2rem;
    align-items: center;
    font-weight: 600;

  }
  
  .header a {
    color: white;
    text-decoration: none;
    font-size: 16px;
    text-size-adjust: 10px;
  }
  
  .header a:hover,
  .header a:active,
  .header a.active {
    color: #95bcf0;
    padding-bottom: 0.15rem;
    border-bottom: 2px solid #95bcf0;
  }